<template>
  <el-drawer
      :model-value="visible"
      title="查看套餐"
      size="550"
      class="custom-drawer"
      :before-close="handleClose"
      @open="handlePackageDetails"
  >
    <div class="detail-content">
      <p>套餐分钟数：{{ packageDetails.packageMinutes }}分钟（每位坐席）</p>
      <p>套餐内分钟数单价：{{ packageDetails.packageInsideUnitPrice }}元/分钟</p>
      <p>套餐外分钟数单价：{{ packageDetails.packageOutsideUnitPrice }}元/分钟</p>
      <p>坐席费用：{{ packageDetails.seatFee }}元/位/月</p>
      <p>中间号月租：{{ packageDetails.midNumberFee }}元/号码（{{packageDetails.midNumberRule}}坐席对应1码号，不足按1码号计算）</p>
      <p>小号月租：{{ packageDetails.smallNumberFee }}元/号码（{{packageDetails.smallNumberRule}}坐席对应1码号）</p>
    </div>
    <template #footer>
      <el-button type="primary" @click="handleClose">关 闭</el-button>
    </template>
  </el-drawer>
</template>
<script lang="ts" setup>
import {packageQueryDetailsApi} from "@api/ec-line-api";
import {ref} from "vue";

const props =  defineProps<{
  visible: boolean;
  packageId: string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
}>();

// 关闭弹框
const handleClose = () => {
  emits("update:visible", false);
};
const packageDetails = ref<any[]>([]);
function handlePackageDetails() {
  const params = {
    id:props.packageId
  }
  packageQueryDetailsApi.getData(params).then((res) => {
    packageDetails.value = res.data;
    console.log('111', packageDetails.value)
  });
}
</script>
<style lang="scss" scoped>
.detail-content{
  p{
    margin-bottom: 50px;
  }
}
</style>
